<template>
	<view style="width: 80%;margin: 100rpx auto;">
		<u-toast ref="uToast"></u-toast>
		<u-text type="primary" text="Catering Regist" align="center" size="30"></u-text>
		<view style="width: 30%;margin: 0 auto;">
			<u-avatar :src="loginForm.avatar" size="100" />
			<u-button @click="chooseAvatar" type="primary" size="small" style="margin-top: 10px;">上传头像</u-button>
		</view>
		<u-form @submit="handleLogin" :model="loginForm">

			<u-form-item label="账号">
				<u-input v-model="loginForm.account" placeholder="请输入账号" />
			</u-form-item>

			<u-form-item label="昵称">
				<u-input v-model="loginForm.nickname" placeholder="请输入昵称" />
			</u-form-item>

			<u-form-item label="密码">
				<u-input type="password" v-model="loginForm.password" placeholder="请输入密码" />
			</u-form-item>

			<!-- 性别 -->
			<u-form-item label="性别" required>
				<u-radio-group v-model="loginForm.sex">
					<u-radio label="男" name="男">男</u-radio>
					<u-radio label="女" name="女">女</u-radio>
				</u-radio-group>
			</u-form-item>

			<!-- 手机号 -->
			<u-form-item label="手机号" required>
				<u-input v-model="loginForm.phone" placeholder="请输入手机号" />
			</u-form-item>

			<!-- 邮箱 -->
			<u-form-item label="邮箱" required>
				<u-input v-model="loginForm.email" placeholder="请输入邮箱" />
			</u-form-item>


			<u-button type="primary" block @click="handleLogin">登录</u-button>

			<view class="register-link">
				<u-text @click="goToRegister" text="有账号?去登录"></u-text>
			</view>
		</u-form>
	</view>


</template>

<script>
	export default {
		options: {
			styleIsolation: 'shared'
		},
		data() {
			return {
				loginForm: {
					account: '',
					password: '',
					role: "user"
				}
			};
		},
		methods: {

			chooseAvatar() {
				uni.chooseImage({
					count: 1,
					success: (res) => {
						const file = res.tempFilePaths[0];
						// 上传头像到服务器
						uni.uploadFile({
							url: this.$api + 'file/upload',
							filePath: file,
							name: 'file',
							success: (uploadRes) => {
								const data = JSON.parse(uploadRes.data);
								if (data.code === 200) {

									this.loginForm = {
										...this.user,
										avatar: this.$api + "file/" + data.data,
										img: data.data
									};
									console.log(this.user.avatar)
									uni.showToast({
										title: '头像更新成功',
										icon: 'success'
									});
								}
							}
						});
					}
				});
			},
			handleLogin() {
				// 这里可以加表单验证，确保用户名和密码不为空
				if (!this.loginForm.account) {
					this.$refs.uToast.show({
						type: "error",
						message: "请输入账号"
					});
					return;
				}

				if (!this.loginForm.password) {
					this.$refs.uToast.show({
						type: "error",
						message: "请输入密码"
					});
					return;
				}


				uni.$u.http.post("login/register", this.loginForm).then(res => {
					if (res.code == 200) {
						this.$refs.uToast.show({
							type: "success",
							message: "注册成功"
						});
						uni.redirectTo({
							url: "/pages/login/login"
						})
					} else {
						this.$refs.uToast.show({
							type: "error",
							message: "注册失败"
						});
					}

				}).catch(error => {
					console.log(error)
					this.$refs.uToast.show({
						type: "error",
						message: "网络错误"
					});
				})
			},

			goToRegister() {
				// 跳转到登录页面
				uni.navigateTo({
					url: '/pages/login/login'
				});
			}
		}
	};
</script>

<style scoped lang="scss">
	.register-link {
		display: block;
		margin-top: 30rpx;
		text-align: right;
		color: #007aff;
		text-decoration: none;
		cursor: pointer;
	}
</style>